button .btn-6 {
  margin: 20px;
}
.custom-btn {
  width: 130px;
  height: 40px;
  color: #fff;
  border-radius: 5px;
  padding: 10px 25px;
  font-family: "Lato", sans-serif;
  font-weight: 500;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.5),
    7px 7px 20px 0px rgba(0, 0, 0, 0.1), 4px 4px 5px 0px rgba(0, 0, 0, 0.1);
  outline: none;
}

/* 6 */
.btn-6 {
  background: rgb(247, 150, 192);
  background: radial-gradient(
    circle,
    rgba(247, 150, 192, 1) 0%,
    rgba(118, 174, 241, 1) 100%
  );
  line-height: 42px;
  padding: 0;
  border: none;
}
.btn-6 span {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}
.btn-6:before,
.btn-6:after {
  position: absolute;
  content: "";
  height: 0%;
  width: 1px;
  box-shadow: -1px -1px 20px 0px rgba(255, 255, 255, 1),
    -4px -4px 5px 0px rgba(255, 255, 255, 1),
    7px 7px 20px 0px rgba(0, 0, 0, 0.4), 4px 4px 5px 0px rgba(0, 0, 0, 0.3);
}
.btn-6:before {
  right: 0;
  top: 0;
  transition: all 500ms ease;
}
.btn-6:after {
  left: 0;
  bottom: 0;
  transition: all 500ms ease;
}
.btn-6:hover {
  background: transparent;
  color: #76aef1;
  box-shadow: none;
}
.btn-6:hover:before {
  transition: all 500ms ease;
  height: 100%;
}
.btn-6:hover:after {
  transition: all 500ms ease;
  height: 100%;
}
.btn-6 span:before,
.btn-6 span:after {
  position: absolute;
  content: "";
  box-shadow: -1px -1px 20px 0px rgba(255, 255, 255, 1),
    -4px -4px 5px 0px rgba(255, 255, 255, 1),
    7px 7px 20px 0px rgba(0, 0, 0, 0.4), 4px 4px 5px 0px rgba(0, 0, 0, 0.3);
}
.btn-6 span:before {
  left: 0;
  top: 0;
  width: 0%;
  height: 0.5px;
  transition: all 500ms ease;
}
.btn-6 span:after {
  right: 0;
  bottom: 0;
  width: 0%;
  height: 0.5px;
  transition: all 500ms ease;
}
.btn-6 span:hover:before {
  width: 100%;
}
.btn-6 span:hover:after {
  width: 100%;
}

#ms-container {
  width: 100%;
  height: 100vh;
  background: #1c9cf2;
}

.ms-content {
  position: absolute;
  width: 200px;
  height: 200px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  border: 10px solid #2ea4f2;
  border-radius: 100%;
}

.ms-content-inside {
  width: 100%;
  height: 100%;
  position: relative;
}

.ms-line-down-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transform: rotate(0deg);
  transition: transform 0s ease;
}
#ms-download {
  display: none;
}
#ms-download:checked ~ .ms-line-down-container {
  transform: rotate(-360deg);
  transition: transform 1.5s ease 1.25s;
}

.ms-line-down {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  width: 10px;
  height: 125px;
  background: #ffffff;
  border-radius: 50px;
  transition: height 0.5s ease;
}

#ms-download:checked ~ .ms-line-down-container .ms-line-down {
  height: 10px;
  top: -20px;
  animation: ms-bounce 0.5s forwards 0.55s;
}
#ms-download:checked ~ .ms-line-down-container .ms-line-down:before {
  content: "";
  position: absolute;
  top: 0;
  left: -94px;
  right: 0;
  bottom: 0;
  width: 200px;
  height: 200px;
  border: 10px solid transparent;
  border-radius: 100%;
  animation: ms-border-fill 0.5s forwards 3s;
}

.ms-line-point {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 58px;
  height: 130px;
  transition: height 0.15s ease 0.45s;
}

#ms-download:checked ~ .ms-line-point {
  height: 60px;
}

#ms-download:checked ~ .ms-line-point:before {
  transform: rotate(90deg);
  right: -10px;
  animation: ms-check-right 0.25s forwards 3s;
}

#ms-download:checked ~ .ms-line-point:after {
  transform: rotate(-90deg);
  left: -10px;
  animation: ms-check-left 0.25s forwards 3s;
}

.ms-line-point:before,
.ms-line-point:after {
  content: "";
  position: absolute;
  width: 10px;
  height: 75px;
  bottom: -10px;
  background: #ffffff;
  border-radius: 50px;
  transition: transform 0.15s ease 0.47s, left 0.15s ease 0.47s,
    right 0.15s ease 0.47s;
}

.ms-line-point:before {
  right: 0px;
  transform: rotate(45deg);
}

.ms-line-point:after {
  left: 0px;
  transform: rotate(-45deg);
}

@keyframes ms-bounce {
  0% {
    top: -20px;
  }
  25% {
    top: -250px;
  }
  50% {
    top: -275px;
  }
  100% {
    top: -190px;
  }
}

@keyframes ms-check-left {
  0% {
    transform: rotate(-90deg);
    left: -10px;
    height: 75px;
  }
  100% {
    transform: rotate(-45deg);
    left: 0px;
    height: 45px;
  }
}

@keyframes ms-check-right {
  0% {
    transform: rotate(90deg);
    right: -10px;
    height: 75px;
    bottom: -10px;
  }
  100% {
    transform: rotate(45deg);
    right: 10px;
    height: 80px;
    bottom: -15px;
  }
}

@keyframes ms-border-fill {
  0% {
    border: 10px solid transparent;
  }
  100% {
    border: 10px solid #ffffff;
  }
}
